*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: sans-serif;

}
body{
    text-align: center;
    display:flex;
    align-items: center;
    justify-content: center;
    height:100vh;
    background-color: #042878;
    color:white;
}
.container p{
    font-family: sans-serif;
    font-size: 2rem;
    font-weight: bold;
    margin: 10px;
}
.btns input[type="button"]{
    font-size:2rem;
    padding:5px 20px;
    margin:10px;
    background-color: antiquewhite;
    color:#042878;
    border-radius: 5px;
    border:none;
    outline: none;
    cursor: pointer;
}